 -webkit-transform 0.7s;\r\n    cursor: pointer;\r\n    transform-style: preserve-3d;\r\n    transform-origin: center right;\r\n    background: linear-gradient(180deg, #fff3187a, #ffc400c7);\r\n}\r\n\r\n.card:hover {\r\n    box-shadow: -1px 5px 15px 0 rgba(0, 0, 0, 0.5);\r\n}\r\n\r\n.rotate {\r\n    position: absolute;\r\n    bottom: 20px;\r\n    right: 20px;\r\n    -webkit-transition: 0.3s;\r\n    transition: 0.3s;\r\n}\r\n\r\n.card.rotateCard.is-flipped {\r\n    transform: translateX(-100%) rotateY(-180deg);\r\n}\r\n\r\n.card__face {\r\n    backface-visibility: hidden;\r\n    cursor: pointer;\r\n}\r\n\r\n.fa-volume-up {\r\n    position: absolute;\r\n    bottom: 20px;\r\n    left: 20px;\r\n    -webkit-transition: 0.3s;\r\n    transition: 0.3s;\r\n    color: #828282;\r\n    padding: 5px;\r\n    border-radius: 10px;\r\n    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.4);\r\n    background-color: white;\r\n}\r\n\r\n.fa-rotate-right {\r\n    color: #828282;\r\n    padding: 5px 7px;\r\n    border-radius: 10px;\r\n    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.4);\r\n    background-color: white;\r\n}\r\n\r\n.fa-volume-up:hover {\r\n    box-shadow: -1px 5px 15px 0 rgba(0, 0, 0, 0.5);\r\n    -webkit-transform: scale(1.3);\r\n    -moz-transform: scale(1.3);\r\n    -o-transform: scale(1.3);\r\n}\r\n\r\n.fa-rotate-right:hover {\r\n    box-shadow: -1px 5px 15px 0 rgba(0, 0, 0, 0.5);\r\n    -webkit-transform: scale(1.3);\r\n    -moz-transform: scale(1.3);\r\n    -o-transform: scale(1.3);\r\n}\r\n\r\n\r\n/********************************/\r\n\r\n.menu-switcher {\r\n    display: flex;\r\n    flex-direction: column;\r\n    height: 26px;\r\n    justify-content: space-between;\r\n    cursor: pointer;\r\n    z-index: 4;\r\n    margin-top: 1vw;\r\n}\r\n\r\n.hamburger {\r\n    display: inline-block;\r\n    height: 4px;\r\n    width: 40px;\r\n    background: #bdc3c7;\r\n    transition: 0.3s;\r\n}\r\n\r\n\r\n/* .hidden {\r\n  left: -100%;\r\n} */\r\n\r\n.sliding-menu {\r\n    height: 100%;\r\n    position: fixed;\r\n    z-index: 2;\r\n    top: 0;\r\n    left: 0;\r\n    width: 20vw;\r\n    transition: 0.5s ease-in-out;\r\n    background: rgb(93, 173, 226);\r\n    background: linear-gradient( 180deg, rgba(93, 173, 226, 1) 0%, rgba(72, 201, 176, 1) 100%, rgba(255, 254, 253, 1) 100%);\r\n}\r\n\r\n.category-links {\r\n    list-style: none;\r\n    margin: 30% 0 0 10%;\r\n    line-height: 2;\r\n}\r\n\r\n.category-links {\r\n    list-style: none;\r\n    margin: 30% 0 0 10%;\r\n    line-height: 2;\r\n}\r\n\r\nli {\r\n    display: list-item;\r\n    text-align: -webkit-match-parent;\r\n}\r\n\r\n.category-link-active {\r\n    font-size: 1.8vw;\r\n    text-decoration: underline;\r\n}\r\n\r\n.category-link {\r\n    text-decoration: none;\r\n    font-size: 1.5vw;\r\n    color: #fff;\r\n    transition: 0.2s;\r\n}\r\n\r\n.category-link:hover {\r\n    font-size: 1.6vw;\r\n    color: rgb(245, 176, 65);\r\n}\r\n\r\n.categories-list {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    justify-content: center;\r\n}\r\n\r\n.switch,\r\n.switch-input {\r\n    position: absolute;\r\n    top: 0;\r\n}\r\n\r\n.switch {\r\n    right: 0;\r\n    display: block;\r\n    display: inline-block;\r\n    vertical-align: top;\r\n    width: 120px;\r\n    height: 40px;\r\n    padding: 3px;\r\n    background-color: #fff;\r\n    border-radius: 5px;\r\n    box-shadow: inset 0 -1px #fff, inset 0 1px 1px rgba(0, 0, 0, 0.05);\r\n    cursor: pointer;\r\n    background-image: linear-gradient(180deg, #eee, #fff 25px);\r\n    box-sizing: content-box;\r\n}\r\n\r\n.switch-input {\r\n    left: 0;\r\n    opacity: 0;\r\n}\r\n\r\n.switch,\r\n.switch-input {\r\n    position: absolute;\r\n    top: 0;\r\n}\r\n\r\n.switch-input:checked~.switch-label {\r\n    background: linear-gradient(40deg, #fff200, #ffc400);\r\n    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.15), inset 0 0 3px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.switch-label {\r\n    position: relative;\r\n    display: block;\r\n    height: inherit;\r\n    font-size: 18px;\r\n    line-height: 40px;\r\n    text-transform: uppercase;\r\n    background: linear-gradient(40deg, #ffd86f, #eb821f);\r\n    border-radius: inherit;\r\n    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.12), inset 0 0 2px rgba(0, 0, 0, 0.15);\r\n    -webkit-transition: 0.15s ease-out;\r\n    transition: 0.15s ease-out;\r\n}\r\n\r\n.switch-input:checked~.switch-label:before {\r\n    opacity: 0;\r\n}\r\n\r\n.switch-label:before {\r\n    content: attr(data-off);\r\n    right: 15px;\r\n    color: black;\r\n    text-shadow: 0 1px hsla(0, 0%, 100%, 0.5);\r\n}\r\n\r\n.switch-label:after,\r\n.switch-label:before {\r\n    position: absolute;\r\n    top: 0;\r\n    -webkit-transition: inherit;\r\n    transition: inherit;\r\n}\r\n\r\n.switch-input:checked~.switch-label:after {\r\n    opacity: 1;\r\n}\r\n\r\n.switch-label:after {\r\n    content: attr(data-on);\r\n    left: 15px;\r\n    color: black;\r\n    text-shadow: 0 1px rgba(0, 0, 0, 0.2);\r\n    opacity: 0;\r\n}\r\n\r\n.switch-label:after,\r\n.switch-label:before {\r\n    position: absolute;\r\n    top: 0;\r\n    -webkit-transition: inherit;\r\n    transition: inherit;\r\n}\r\n\r\n.switch-input:checked~.switch-handle {\r\n    left: 85px;\r\n    box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.switch-handle {\r\n    position: absolute;\r\n    top: 6px;\r\n    left: 6px;\r\n    width: 33px;\r\n    height: 33px;\r\n    background: #fff;\r\n    border-radius: 5px;\r\n    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);\r\n    background-image: -webkit-gradient( linear, left top, left bottom, color-stop(40%, #fff), to(#f0f0f0));\r\n    background-image: linear-gradient(180deg, #fff 40%, #f0f0f0);\r\n    -webkit-transition: left 0.15s ease-out;\r\n    transition: left 0.15s ease-out;\r\n}\r\n\r\n.switch-handle:before {\r\n    content: \"\";\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 50%;\r\n    margin: -6px 0 0 -6px;\r\n    width: 12px;\r\n    height: 12px;\r\n    background: #f9f9f9;\r\n    border-radius: 6px;\r\n    box-shadow: inset 0 1px rgba(0, 0, 0, 0.02);\r\n    background-image: -webkit-gradient( linear, left top, left bottom, from(#eee), to(#fff));\r\n    background-image: linear-gradient(180deg, #eee, #fff);\r\n}\r\n\r\n.main-link {\r\n    display: inline-flex;\r\n    width: 120px;\r\n    height: 42px;\r\n    font-size: 20px;\r\n    text-align: center;\r\n    justify-content: center;\r\n    align-items: center;\r\n    color: black;\r\n    text-decoration: none;\r\n    transition: 0.2s;\r\n}\r\n\r\n.main-link:hover {\r\n    color: rgb(245, 176, 65);\r\n}\r\n\r\n.back-to-main {\r\n    display: flex;\r\n    position: relative;\r\n    outline: none;\r\n    user-select: none;\r\n    border: none;\r\n    text-align: center;\r\n    height: max-content;\r\n    background: linear-gradient(40deg, #fff200, #ffc400);\r\n    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.15), inset 0 0 3px rgba(0, 0, 0, 0.2);\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n    transition: 0.2s;\r\n}\r\n\r\n.toggle {\r\n    position: relative;\r\n    display: inline;\r\n}\r\n\r\n.switch-container::after {\r\n    box-sizing: inherit;\r\n}\r\n\r\n.switch-container::before {\r\n    box-sizing: inherit;\r\n}\r\n\r\n.switch-container {\r\n    display: flex;\r\n    position: relative;\r\n    padding-bottom: 45px;\r\n    width: 120px;\r\n}\r\n\r\n\r\n/***************************/\r\n\r\n.mainPage {\r\n    background-color: blue;\r\n    width: 5rem;\r\n    height: 5rem;\r\n}\r\n\r\n.contentAll {\r\n    display: flex;\r\n    flex-wrap: wrap;\r\n    justify-content: center;\r\n    padding: 15px 15px;\r\n    max-width: 1440px;\r\n    margin: auto;\r\n}\r\n\r\n.view {\r\n    border-radius: 10%;\r\n}\r\n\r\n.overlay .mask {\r\n    opacity: 0;\r\n    -webkit-transition: all 0.4s ease-in-out;\r\n    transition: all 0.4s ease-in-out;\r\n}\r\n\r\n.view .mask {\r\n    position: absolute;\r\n    top: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    overflow: hidden;\r\n    background-attachment: fixed;\r\n}\r\n\r\n.rgba-white-slight,\r\n.rgba-white-slight:after {\r\n    background-color: rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.overlay .mask:hover {\r\n    opacity: 1;\r\n}\r\n\r\n.header {\r\n    width: 80%;\r\n    margin: 0 auto;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    flex-wrap: wrap;\r\n}\r\n\r\n\r\n/**************************************************************/\r\n\r\n#menu {\r\n    z-index: 2;\r\n}\r\n\r\n#menu-bar {\r\n    width: 45px;\r\n    height: 40px;\r\n    margin: 42px 0 20px 20px;\r\n    cursor: pointer;\r\n}\r\n\r\n.bar {\r\n    height: 5px;\r\n    width: 100%;\r\n    background: linear-gradient(180deg, #fff3187a, #ffc400c7);\r\n    display: block;\r\n    border-radius: 5px;\r\n    transition: 0.3s ease;\r\n}\r\n\r\n#bar1 {\r\n    transform: translateY(-4px);\r\n}\r\n\r\n#bar3 {\r\n    transform: translateY(4px);\r\n}\r\n\r\n.nav {\r\n    transition: 0.3s ease;\r\n    display: none;\r\n}\r\n\r\n.nav ul {\r\n    padding: 0 22px;\r\n}\r\n\r\n.nav li {\r\n    list-style: none;\r\n    padding: 12px 0;\r\n}\r\n\r\n.nav li a {\r\n    color: white;\r\n    font-size: 20px;\r\n    text-decoration: none;\r\n    padding: 10px;\r\n}\r\n\r\n.nav li a:hover {\r\n    background-color: #caab1f;\r\n    border-radius: 10px;\r\n    padding: 10px;\r\n    color: black;\r\n}\r\n\r\n.menu-bg,\r\n#menu {\r\n    top: 0;\r\n    left: 0;\r\n    position: absolute;\r\n    display: flex;\r\n}\r\n\r\n.menu-bg {\r\n    z-index: 1;\r\n    width: 0;\r\n    height: 0;\r\n    margin: 30px 0 20px 20px;\r\n    background: linear-gradient(180deg, #000000a1, #000000);\r\n    border-radius: 50%;\r\n    transition: 0.3s ease;\r\n}\r\n\r\n.change {\r\n    display: block;\r\n}\r\n\r\n.change .bar {\r\n    background-color: white;\r\n}\r\n\r\n.change #bar1 {\r\n    transform: translateY(4px) rotateZ(-45deg);\r\n}\r\n\r\n.change #bar2 {\r\n    opacity: 0;\r\n}\r\n\r\n.change #bar3 {\